import uCharts from '@/components/u-charts/u-charts.js';
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import uniDrawer from '@/components/uni-drawer/uni-drawer.vue'
import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'
import sellerInfoMatch from "@/components/inquiry/sellerInfoMatch.vue"
var _self;
var canvaLineA=null;
export default {
	components:{
		faIcon,
		uniDrawer,
		uniCalendar,
		sellerInfoMatch
	},
	data() {
		return {
			quotationInfo:'',
			goodsCode:'',
			color1:'#ff6000',
			startTime:'',
			endTime:'',
			showRight:false,
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			series:[
				{
					name: "塔丝隆50D",
					color: "#FF6000",
					data: [1.81,2, 2.01, 2.03, 2.38, 5.27,2.03, 2.38, 5.27],
					legendShape: "line",
					pointShape: "circle",
					show: true,
					type: "line",
					format:(val)=>{return val+'元'}
				}
			],
			categories:['10.01','10.01','10.14','10.24','10.30','11.23','10.24','10.30','11.23']
			
			
		}
	},
	onLoad(options) {
		_self = this;
		this.goodsCode = options.goodsCode
		this.startTime = options.startTime
		this.endTime = options.endTime
		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		//this.getServerData();
		// this.endTime = this.getDay(0)  //设置当前时间
		// this.startTime = this.getDay(-7)  //默认是一周前
		console.log(this.endTime)
		console.log(this.startTime)
		this.matchList3()
	},
	methods: {
		matchList3:function(){
			let data={
				goodsCode: _self.goodsCode,
				startTime: _self.startTime,
				endTime:   _self.endTime
			}
			let url = this.Api.matchList3
			this.myRequest(data,url,'get').then(res => {
			  console.log(res);
			  if (res.data.status==0){
				  _self.quotationInfo = res.data.data
				  _self.categories = res.data.data.categories
				  console.log(_self.categories)
				  _self.series = res.data.data.series
				  console.log(_self.series)
				  _self.series.forEach((item)=>{
					  this.$set(item,'legendShape','line')
					  this.$set(item,'pointShape','circle')
					  this.$set(item,'show',true)
					  this.$set(item,'type','line')
				  })
				  let LineA={categories:[],series:[]};
				  //这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
				  // LineA.categories=res.data.data.LineA.categories;
				  LineA.categories= _self.categories
				  
				  // LineA.series=res.data.data.LineA.series;
				  LineA.series= _self.series
				  _self.showLineA("canvasLineA",LineA);
				  console.log(LineA.series[0])
			  }
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		weekly:function(){
			this.startTime= this.getDay(-7)
			this.endTime = this.getDay(0)
			this.matchList3()
		},
		resetCal:function(){
			this.startTime= this.getDay(-7)
			this.endTime = this.getDay(0)
		},
		confirmCal:function(){
			this.showRight = false
			this.matchList3()
		},
		showCalendarEnd:function(){
			this.$refs.calendarEnd.open();
		},
		showCalendar:function(){
			this.$refs.calendar.open();
		},
		confirm:function(e){
			console.log(e)
			this.startTime = e.fulldate
			
		},
		confirmEnd:function(e){
			console.log(e)
			this.endTime = e.fulldate
			
		},
		closeDrawer:function(){
			this.showRight = false
		},
		showDrawer:function(){
			this.showRight = true
			console.log(this.showRight)
		},
		toQuotateDetail:function(quotationNumber){
			uni.navigateTo({
				url: './quotationDetail?quotationNumber='+quotationNumber,
				success: res => {},
				fail: (err) => { console.log(err)},
				complete: () => {}
			});
		},
		getServerData(){
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data:{
				},
				success: function(res) {
					console.log(res.data.data)
					let LineA={categories:[],series:[]};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					// LineA.categories=res.data.data.LineA.categories;
					LineA.categories= _self.categories
					
					// LineA.series=res.data.data.LineA.series;
					LineA.series= _self.series
					_self.showLineA("canvasLineA",LineA);
					console.log(LineA.series[0])
				},
				fail: () => {
					_self.tips="网络错误，小程序端请检查合法域名";
				},
			});
		},
		showLineA(canvasId,chartData){
			canvaLineA=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'line',
				fontSize:11,
				legend:{show:true},
				dataLabel:true,
				dataPointShape:true,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					disableGrid:true,
					titleFontColor:'#333333'
				},
				yAxis: {
					gridType:'dash',
					gridColor:'#EAEAEA',
					
					
				},
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					line:{
						type: 'straight'
					}
				}
			});
			
		},
		touchLineA(e) {
			canvaLineA.showToolTip(e, {
				format: function (item, category) {
					// return category + ' ' + item.name + ':' + item.data 
					return item.data 
				}
			});
		},
		getDay:function(day){
		  
		 　　var today = new Date();
		  
		
		  
		 　　var targetday_milliseconds=today.getTime() + 1000*60*60*24*day;
		  
		  
		  
		 　　today.setTime(targetday_milliseconds); //注意，这行是关键代码
		  
		  
		  
		 　　var tYear = today.getFullYear();
		  
		 　　var tMonth = today.getMonth();
		  
		 　　var tDate = today.getDate();
		  
		 　　tMonth = this.doHandleMonth(tMonth + 1);
		  
		 　　tDate = this.doHandleMonth(tDate);
		  
		 　　return tYear+"-"+tMonth+"-"+tDate;
		  
		 },
		  
		 doHandleMonth:function(month){
		  
		 　　var m = month;
		  
		 　　if(month.toString().length == 1){
		  
		 　　　　m = "0" + month;
		 
		 　　}
		  
		 　　return m;
		  
		 }
		
	}
}
